<template>
	<div id="detail">
	  <details-nav-bar class="detail-navbar" @titleclick="titleclick" ref="nav"/>
	  <scroll class="content" ref="scroll" :probe-type="3" @scroll="getScrollPosition">
	 
	  <details-swiper :top-images="topImages"/>
        <details-base-info :goods="goods"></details-base-info>
        <details-shop-info :shop="shop"></details-shop-info>
        <details-goods-info :detail-info="detailInfo" @imageLoad="imageLoad"></details-goods-info>
        <details-param-info :param-info="paramInfo" ref="params"></details-param-info>
        <goods-list :goods="recommends" ref="recommend"></goods-list>
	  </scroll>
   <details-bottom-bar @addCart="addTOCart"></details-bottom-bar>
	</div>
</template>

<script>
	import DetailsNavBar from "./childComps/DetailsNavBar";
	import DetailsSwiper from "./childComps/DetailsSwiper";
	import DetailsBaseInfo from "./childComps/DetailsBaseInfo";
	import DetailsShopInfo from "./childComps/DetailsShopInfo";
	import DetailsGoodsInfo from "./childComps/DetailsGoodsInfo";
	import DetailsParamInfo from "./childComps/DetailsParamInfo";
	import DetailsBottomBar from "./childComps/DetailsBottomBar";
    import {getDtail,Goods,Shop,GoodsParam,getRecommend} from "network/detail";
    import GoodsList from "components/contents/goods/GoodsList";
    import Scroll from 'components/common/scroll/Scroll'
    import {debounce} from "common/utils"  
    import Backtop from "components/contents/backtop/Backtop"

    export default {
        name: "Details",
        components:{
        DetailsNavBar,
        DetailsSwiper,
        DetailsBaseInfo,
        DetailsShopInfo,
        DetailsBottomBar,
        Scroll,
        Backtop,
        DetailsGoodsInfo,
        DetailsParamInfo,
        GoodsList
        },
        data(){
        	return{
        		iid:null,
        		topImages:[],
        		goods:{},
        		shop:{},
        		detailInfo:{},
        		paramInfo:{},
        		recommends:[],
        		Ytops:[],
        		getYtops:null,
        		currentIndex:0,
        		count:0
        	}
        },
        created(){
        	//1.保存传入的iid
        	this.iid=this.$route.params.iid
        	//2.根据iid请求详情数据
        	getDtail(this.iid).then(res => {
        		console.log(res)
        		const datal =res.result
        		//1.获取顶部轮播图
        		this.topImages=datal.itemInfo.topImages
        		//2.获取商品信息columns,services
        		this.goods=new Goods(datal.itemInfo,datal.columns,datal.shopInfo.services)
        		//3.获取店铺信息
        		this.shop = new Shop(datal.shopInfo)
        		//4.商品详细信息
          		this.detailInfo = datal.detailInfo;
          		//商品参数信息
          		this.paramInfo = new GoodsParam(datal.itemParams.info, datal.itemParams.rule)
        	})
        	//3.请求推荐数据
        	 getRecommend().then(res => {
        		this.recommends=res.data.list
        		})
        	  //4.给gettopy赋值
        	 this.getYtops=debounce(()=>{
        	 	this.Ytops=[];
        	 	this.Ytops.push(0);
        	 	this.Ytops.push(this.$refs.params.$el.offsetTop);
        	 	this.Ytops.push(this.$refs.recommend.$el.offsetTop);
        	 	this.Ytops.push(Number.MAX_VALUE);      	 	
        	 },100)
        	
        },
        mounted(){
       	//3.监听item中图片数据加载完成,denounce防抖函
       	this.$bus.$on('detailitemimageload',()=>{
       		refresh()
       		
       	})
       },
        methods:{
        	imageLoad(){
        		this.$refs.scroll.refresh()
        		this.getYtops()
        	},
        	titleclick(index){
        		this.$refs.scroll.scrolltop(0,-this.Ytops[index],200)
        	},
        	contentScroll(position){
        		const positionY =-position.Y
        		let length=this.Ytops.length
        		for(let i=0; i<length-1; i++){
        			if(this.currentIndex!==i && (positionY>=this.Ytops[i] && positionY<this.Ytops[i+1]) ){
        				this.currentIndex=i;
        				this.$refs.nav.currentIndex=this.currentIndex;
        				console.log("aaa")
        			}
        		}
        
        	},
        	getScrollPosition(position) {
        let i = 0;
        if(-position.y >= 0 && -position.y < this.Ytops[1]) {
            i = 0;
        }
        if(-position.y >= this.Ytops[1] && -position.y < this.Ytops[2]) {
          i = 1;
        }
        
        if(-position.y >= this.Ytops[2]) {
          i = 2;
        }
        // this.currentIndex = i;
        this.$refs.nav.currentIndex = i;
      },
        addTOCart(){
        	const product ={}
        		product.images=this.topImages[0]
        		product.title=this.goods.title
        		product.price=this.goods.realPrice
        		product.count=this.count
        		product.iid=this.iid
        	
//      	this.$store.commit('addcart',product)
        	this.$store.commit('addcart',product)
        	}
        }
        }
    
</script>

<style scoped>
#detail{
	position: relative;
	z-index: 9;
	background-color: #fff;
	height: 100vh;
}
.content{
	height: calc(100% - 44px - 49px);
}
.detail-navbar{
	position: relative;
	z-index: 9;
	background-color: #fff;
}
</style>