<template>
    <nav-bar>
    	<div slot="left" class="back" @click="backhome"><img src="~assets/images/common/back.svg" alt="" /></div>
    	<div slot="center" class="details" >
    		<div v-for="(item,index) in titles" class="details-item" 
    			:class="{active:index===currentIndex}" @click="itemselect(index)">{{item}}
    		</div>
    	</div>
    	</nav-bar>  
</template>

<script>
	import NavBar from "components/common/navbar/NavBar";
    export default {
        name: "DetailsNavBar",
        components:{
        NavBar
        },
        data(){
        	return{
        		titles:['商品','参数','推荐'],
        		currentIndex:0
        	}
        },
        methods:{
        	itemselect(index){
        		this.currentIndex=index;
        		this.$emit('titleclick',index)
        	},
        	backhome(){
        		this.$router.back()
        	}
        }
    }
</script>

<style scoped>
.details{
	display: flex;
	font-size: 12px
}
.details-item{
	flex: 1;
}
.active{
	color: red;
}
.back img{
	margin-top: 12px;
}
</style>